.sidebar {
  position: -webkit-sticky;
  position: sticky;
  top: 170px;
  background: #FFF;
  padding: 30px;
  border-radius: 20px;
  &__title {
    display: block;
    padding: 5px 10px;
    color: $text;
    font-size: 12px;
    line-height: 18px;
    font-weight: 700; }
  &__menu {
    list-style: none;
    padding-left: 0;
    margin: 0 0 30px;
    &:last-child {
      margin: 0; }
    &> li {
      display: block;
      width: 100%;
      padding: 10px;
      &> a {
        color: $text;
        width: 100%;
        display: flex;
        align-items: center;
        .icon {
          display: block;
          width: 32px;
          height: 32px;
          object-fit: contain;
          object-position: center; }
        span {
          display: block;
          padding-left: 15px; }
        &:hover {
          color: $primary;
          text-decoration: none; } } } }
  @include mac14() {
    top: 140px;
    padding: 20px; }
  @include maxtablet() {
    position: fixed;
    z-index: 200;
    border-radius: 0;
    top: 0;
    left: auto;
    bottom: 0;
    right: 100%;
    width: 100%;
    height: 100%;
    padding: 0;
    transition: all .3s ease-in-out;
    &__toggle {
      position: fixed;
      z-index: 5;
      top: 140px;
      left: -1px;
      width: 40px;
      height: 37px;
      background: #FFF;
      padding: 6px;
      border-radius: 0 4px 4px 0;
      box-shadow: 0px 10px 50px rgba(0, 0, 0, 0.1);
      border: 1px solid $lightblue;
      transition: all .3s ease-in-out;
      span {
        position: absolute;
        top: 8px;
        width: calc(100% - 12px);
        height: 3px;
        background: $secondary;
        border-radius: 2px;
        transition: all .3s ease-in-out;
        &:nth-child(2) {
          top: 16px; }
        &:nth-child(3) {
          top: 24px; } } }
    &__wrap {
      max-height: 100vh;
      overflow: auto;
      padding: 80px 15px 40px; }
    &.open {
      right: 0;
      .sidebar__toggle {
        top: 10px;
        left: 10px;
        border-radius: 4px;
        span {
          transform: rotate(45deg);
          top: 16px;
          border-radius: 0;
          &:nth-child(2) {
            transform: rotate(-45deg); }
          &:nth-child(3) {
            display: none; } } } } } }

.content {
  background: #FFF;
  padding: 30px 40px;
  border-radius: 20px;
  .btn-wrap {
    margin: -5px;
    .btn {
