  &__tabs {
    .nav {
      position: relative;
      display: flex;
      width: 100%;
      background: #EEEEEE;
      border-radius: 100px;
      padding: 0;
      counter-reset: panel-tabs;
      margin: 0;
      &::before,
      &::after {
        content: "";
        display: block;
        width: 100%;
        position: absolute;
        left: 0; }
      &::before {
        z-index: 1;
        background: #DDDDDD;
        top: 50%;
        height: 50%; }
      &::after {
        z-index: 2;
        border-radius: 100px;
        background: #EEEEEE;
        top: 0;
        height: 100%; }
      .nav-item {
        z-index: 3;
        position: relative;
        display: block;
        width: 100%;
        max-width: 100%;
        counter-increment: panel-tabs; }
      .nav-link {
        position: relative;
        display: block;
        font-size: 20px;
        line-height: 30px;
        padding: 25px 30px 25px 90px;
        border-radius: 100px;
        white-space: nowrap;
        color: #C5C6C9;
        transition: all .3s ease-in-out;
        &::before {
          content: counter(panel-tabs);
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          height: 36px;
          width: 36px;
          border-radius: 18px;
          top: calc(50% - 18px);
          left: 30px;
          background: #C5C6C9;
          color: #FFF;
          font-weight: 700;
          transition: all .3s ease-in-out; }
        &:hover {
          color: $secondary;
          text-decoration: none;
          &::before {
            background: $secondary; } }
        &.active {
          color: #FFF;
          background: $primary;
          &::before {
            background: $secondary; } } } }
