            &::before {
              content: "";
              display: block;
              width: 15px;
              height: 15px;
              position: absolute;
              top: 4px;
              left: 0;
              background-image: url(../images/ic-date-white.svg);
              background-repeat: no-repeat;
              background-position: center;
              background-size: contain; } }
