main {
  background: #EEEEEE;
  min-height: calc(100vh - 200px);
  width: 100%;
  margin-top: 120px;
  padding: 50px 0;
  @include mac14() {
    padding-top: 30px;
    padding-bottom: 30px; }
  @include maxtablet() {
    padding: 30px 0;
    margin-top: 84px; } }

.container {
  max-width: 1520px;
  @include mobile {
    max-width: 100%; } }

.row {
  margin-left: -10px;
  margin-right: -10px; }

[class*='col-'] {
  padding-left: 10px;
  padding-right: 10px; }

