  .cel-lg-5 {
    -ms-flex: 0 0 50%;
    flex: 0 0 50%;
    max-width: 50%; }

  .cel-lg-6 {
    -ms-flex: 0 0 60%;
    flex: 0 0 60%;
    max-width: 60%; }

  .cel-lg-7 {
    -ms-flex: 0 0 70%;
    flex: 0 0 70%;
    max-width: 70%; }

  .cel-lg-8 {
    -ms-flex: 0 0 80%;
