    max-width: 100%; } }

ul,
ol {
  &.twocol {
    columns: 2;
    column-gap: rem(20); } }

// BUTTON
.btn {
  display: inline-block;
  font-weight: 500;
  color: $text;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 24px;
  border-radius: 30px;
  transition: all .3s ease-in-out;
  text-transform: uppercase;
  &.btn-r10 {
    border-radius: 10px; }
  &:hover {
    color: $text;
    text-decoration: none; }
  &:focus, &.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25); }
  &.disabled, &:disabled {
    opacity: 0.65; }
  &.btn-small {
    font-size: 14px;
    line-height: 20px;
    padding: 5px 16px; }
  &.btn-icon {
    white-space: nowrap;
    .icon {
      display: inline-block;
      margin-right: 15px; }
    span {
      display: inline-block; } }
  & + .btn {
    margin-left: 16px; } }

.btn-primary {
  color: #FFFFFF !important;
  background-color: $primary;
  border-color: $primary;
  &:hover {
    color: #FFFFFF;
    background-color: darken($primary, 15%);
    border-color: darken($primary, 15%); }
  &:focus, &.focus {
    color: #FFFFFF;
    background-color: darken($primary, 5%);
    border-color: darken($primary, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: #FFFFFF;
    background-color: darken($primary, 5%);
    border-color: darken($primary, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-secondary {
  color: #FFFFFF;
  background-color: $secondary;
  border-color: $secondary;
  &:hover {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%); }
  &:focus, &.focus {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: #FFFFFF;
    background-color: darken($secondary, 5%);
    border-color: darken($secondary, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-light {
  color: $primary;
  background-color: $lightblue;
  border-color: $lightblue;
  &:hover {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%); }
  &:focus, &.focus {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: $primary;
    background-color: darken($lightblue, 5%);
    border-color: darken($lightblue, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-warning {
  color: #333333;
  background-color: #F3C044;
  border-color: #F3C044;
  @include font-500;
  &:hover {
    color: #FFFFFF;
    background-color: darken(#F3C044, 5%);
    border-color: darken(#F3C044, 5%); }
  &:focus, &.focus {
    color: #FFFFFF;
    background-color: darken(#F3C044, 5%);
    border-color: darken(#F3C044, 5%);
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); }
  &.disabled, &:disabled {
    pointer-events: none;
    color: #FFFFFF;
    background-color: $border;
    border-color: $border; }
  &:not(:disabled):not(.disabled):active, &:not(:disabled):not(.disabled).active, .show > &.dropdown-toggle {
    color: #FFFFFF;
    background-color: darken(#F3C044, 5%);
    border-color: darken(#F3C044, 5%); }
  &:not(:disabled):not(.disabled):active:focus, &:not(:disabled):not(.disabled).active:focus, .show > &.dropdown-toggle:focus {
    box-shadow: 0 0 0 0.2rem rgba(38, 143, 255, 0.5); } }

.btn-transparent {
  color: $primary;
  &:hover {
    color: $secondary; } }

.btn-border {
  border: 1px solid #0083CB;
  color: #0083CB;
  background: #fff;
  &:hover {
    background: #0083CB;
    color: #FFF; } }

.btn-next {
  &::after {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-left: 20px;
    width: 16px;
    height: 16px;
    background: url(../images/ic-next.svg) no-repeat;
    background-position: center;
    background-size: contain; } }

.btn-back {
  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
    width: 16px;
    height: 16px;
    background: url(../images/ic-back.svg) no-repeat;
    background-position: center;
    background-size: contain; } }

.btn-add {
  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
    width: 16px;
    height: 16px;
    background: url(../images/ic-add.svg) no-repeat;
    background-position: center;
    background-size: contain; } }

.content {
  &.full {
    margin-left: -25%;
    width: 125%; }
  @include maxtablet() {
    &.full {
      margin-left: 0;
      width: 100%; } } }

// BADGE
.badge {
  text-align: center;
  display: inline-block;
  white-space: nowrap;
  padding: 4px 15px;
  background: #CAD5E4;
  border-radius: 10px;
  color: $secondary;
  @include font-500(); }
.badge-full {
  display: block; }
.badge-light {
  color: #0083CB;
  background: $lightblue; }
.badge-primary {
  background: $primary; }
.badge-secondary {
  color: #FFF;
  background: $secondary; }

// PAGINATION
.pagination {
  display: flex;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  .page-link {
    color: $primary;
    text-align: center;
    min-width: 40px;
    display: block;
    font-size: 16px;
    line-height: 20px;
    padding: 10px;
    border-radius: 8px;
    transition: all .3s ease;
    &:hover {
      text-decoration: none;
      font-weight: 700;
      color: $secondary; }
    &.prev {
      margin-right: 40px; }
    &.next {
      margin-left: 40px; } }
  span.page-link {
    pointer-events: none; }
  .page-item.active {
    .page-link {
      color: #FFF;
      background: $primary; } }
  @include maxtablet() {
    .page-link {
      padding: 4px;
      font-size: 14px;
      line-height: 20px;
      min-width: 28px;
      &.prev {
        margin-right: 15px; }
      &.next {
        margin-left: 15px; } } } }

// Alert
.alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
  &-dismissible {
    padding-right: 4rem;
    .close {
      position: absolute;
      top: 0;
      right: 0;
      padding: 0.75rem 1.25rem;
      color: inherit; } }
  &-primary {
    color: #004085;
    background-color: #cce5ff;
    border-color: #b8daff; }
  &-success {
    color: #155724;
    background-color: #d4edda;
    border-color: #c3e6cb; }
  &-warning {
    color: #856404;
    background-color: #fff3cd;
    border-color: #ffeeba; }
  &-danger {
    color: #721c24;
    background-color: #f8d7da;
    border-color: #f5c6cb; }
  .close {
    float: right;
    font-size: 1.5rem;
    font-weight: 700;
    line-height: 1;
    color: #000;
    text-shadow: 0 1px 0 #fff;
    opacity: .5;
    border: 0;
    background: transparent;
    &:focus,
    &:hover,
    &:visited {
      outline: none;
      box-shadow: none; } } }

