/* ------------------------------------------------------------------------------------------------ */
/* Custom Plugins */
/* ------------------------------------------------------------------------------------------------ */

.dropup,
.dropright,
.dropdown,
.dropleft {
  position: relative; }

.dropdown-toggle {
  white-space: nowrap;
  &::after {
    display: inline-block;
    content: "";
    vertical-align: middle;
    width: 15px;
    height: 15px;
    background: url(../images/ic-carret-down.svg) no-repeat;
    background-size: contain;
    background-position: center;
    margin-left: 20px; }
  &:empty::after {
    margin-left: 0; } }

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
  display: none;
  float: left;
  min-width: 10rem;
  padding: 0.5rem 0;
  margin: 0.125rem 0 0;
  font-size: 1rem;
  color: #212529;
  text-align: left;
  list-style: none;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  &-left {
    right: auto;
    left: 0; }
  &-right {
    right: 0;
    left: auto; } }

@media (min-width: 576px) {
  .dropdown-menu-sm-left {
    right: auto;
    left: 0; }

  .dropdown-menu-sm-right {
    right: 0;
    left: auto; } }

@media (min-width: 768px) {
  .dropdown-menu-md-left {
    right: auto;
    left: 0; }

  .dropdown-menu-md-right {
    right: 0;
    left: auto; } }

@media (min-width: 992px) {
  .dropdown-menu-lg-left {
    right: auto;
    left: 0; }

  .dropdown-menu-lg-right {
    right: 0;
    left: auto; } }

@media (min-width: 1200px) {
  .dropdown-menu-xl-left {
    right: auto;
    left: 0; }

  .dropdown-menu-xl-right {
    right: 0;
    left: auto; } }

.dropup .dropdown-menu {
  top: auto;
  bottom: 100%;
  margin-top: 0;
  margin-bottom: 0.125rem; }

.dropup .dropdown-toggle::after {
  display: inline-block;
  content: "";
  transform: rotate(180deg); }

.dropup .dropdown-toggle:empty::after {
  margin-left: 0; }

.dropright .dropdown-menu {
  top: 0;
  right: auto;
  left: 100%;
  margin-top: 0;
  margin-left: 0.125rem; }

.dropright .dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0.3em solid transparent;
  border-right: 0;
  border-bottom: 0.3em solid transparent;
  border-left: 0.3em solid; }

.dropright .dropdown-toggle:empty::after {
  margin-left: 0; }

.dropright .dropdown-toggle::after {
  vertical-align: 0; }

.dropleft .dropdown-menu {
  top: 0;
  right: 100%;
  left: auto;
  margin-top: 0;
  margin-right: 0.125rem; }

.dropleft .dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.255em;
  vertical-align: 0.255em;
  content: ""; }

.dropleft .dropdown-toggle::after {
  display: none; }

.dropleft .dropdown-toggle::before {
  display: inline-block;
  margin-right: 0.255em;
  vertical-align: 0.255em;
  content: "";
  border-top: 0.3em solid transparent;
  border-right: 0.3em solid;
  border-bottom: 0.3em solid transparent; }

.dropleft .dropdown-toggle:empty::after {
  margin-left: 0; }

.dropleft .dropdown-toggle::before {
  vertical-align: 0; }

.dropdown-menu[x-placement^="top"], .dropdown-menu[x-placement^="right"], .dropdown-menu[x-placement^="bottom"], .dropdown-menu[x-placement^="left"] {
  right: auto;
  bottom: auto; }

.dropdown-divider {
  height: 0;
  margin: 0.5rem 0;
  overflow: hidden;
  border-top: 1px solid #e9ecef; }

.dropdown-item {
  display: block;
  width: 100%;
  padding: 12px 20px;
  clear: both;
  font-weight: 400;
  color: #212529;
  text-align: inherit;
  white-space: nowrap;
  background-color: transparent;
  border: 0;

  &:hover, &:focus {
    color: #FFFFFF !important;
    text-decoration: none;
    background-color: $primary; }

  &.active, &:active {
    color: #FFFFFF;
    text-decoration: none;
    background-color: $primary; }

  &.disabled, &:disabled {
    color: #6c757d;
    pointer-events: none;
    background-color: transparent; } }

.dropdown-menu.show {
  display: block; }

.dropdown-header {
  display: block;
  padding: 0.5rem 1.5rem;
  margin-bottom: 0;
  font-size: 0.875rem;
  color: #6c757d;
  white-space: nowrap; }

.dropdown-item-text {
  display: block;
  padding: 0.25rem 1.5rem;
  color: #212529; }

.table {
  table-layout: fixed;
  width: 100%;
  margin-bottom: 1rem;
  color: $text; }
.table th,
.table td {
  padding: 15px 20px;
  vertical-align: top;
  .btn-wrap {
    margin: -4px;
    position: relative;
    .btn {
      margin: 4px;
      @include font-500(); } }
  .badge-wrap {
    margin: -4px;
    position: relative;
    .badge {
      margin: 4px; } } }

.table thead td {
  vertical-align: bottom;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #FFF;
  background: $secondary;
  white-space: nowrap;
  &:first-child {
    border-radius: 10px 0 0 10px; }
  &:last-child {
    border-radius: 0 10px 10px 0; } }

.table tbody td {
  color: #5F2011;
  .text-date {
    color: #0083CB;
    font-style: italic;
    display: block; } }

.table tbody tr:nth-of-type(even) {
  background-color: #EEF9FF; }

.table tbody tr:last-child {
  td:first-child {
    border-radius: 0 0 0 10px; }
  td:last-child {
    border-radius: 0 0 10px 0; } }

// Modal
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  outline: 0; }

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none; }

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0; }

.modal-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 1rem;
  border-bottom: 1px solid #dee2e6;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem; }

.modal-title {
  margin-bottom: 0;
  line-height: 1.5; }

.modal-body {
  position: relative;
  flex: 1 1 auto;
  padding: 1rem; }

.modal-footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-end;
  padding: 0.75rem;
  border-top: 1px solid #dee2e6;
  border-bottom-right-radius: 0.3rem;
  border-bottom-left-radius: 0.3rem;

  .modal-open {}
  overflow: hidden; }

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background-color: #000;
  &.show {
    opacity: .5; } }

.modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll; }

.modal-scrollbar-measure::-webkit-scrollbar {
  width: 100%;
  height: 100%; }

.modal-scrollbar-measure::-webkit-scrollbar-track {
  background: none; }

.modal-scrollbar-measure::-webkit-scrollbar-thumb {
  background: #888; }

.modal-scrollbar-measure::-moz-scrollbar {
  width: 100%;
  height: 100%; }

.modal-scrollbar-measure::-moz-scrollbar-track {
  background: none; }

.modal-scrollbar-measure::-moz-scrollbar-thumb {
  background: #888; }

.modal-scrollbar-measure::-ms-scrollbar {
  width: 100%;
  height: 100%; }

.modal-scrollbar-measure::-ms-scrollbar-track {
  background: none; }

.modal-scrollbar-measure::-ms-scrollbar-thumb {
  background: #888; }

.modal-dialog {
  position: relative;
  width: auto;
  margin: 0.5rem;
  pointer-events: none;
  margin: 0 auto; }

.modal-content {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  pointer-events: auto;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.3rem;
  outline: 0; }

// Modal kecil
.modal-dialog.modal-sm {
  max-width: 300px; }

// Modal sedang
.modal-dialog.modal-md {
  max-width: 500px; }

// Modal besar
.modal-dialog.modal-lg {
  max-width: 800px; }

// Modal sangat besar
.modal-dialog.modal-xl {
  max-width: 1140px; }


.modal-dialog.modal-dialog-centered {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: calc(100% - 1rem); }

.modal-content {
  margin: 0; }


.close {
  float: right;
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1;
  color: #000;
  text-shadow: 0 1px 0 #fff;
  opacity: 0.5; }

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
  opacity: 0.75; }

.close:not(:disabled):not(.disabled) {
  cursor: pointer; }

